.about {
  font-size: .75rem;
  display: flex;
  margin: auto;
  justify-content: space-around;
  align-items: center;
  width: 90%;
  position: relative;
  
  .container__about__logo {
    .about__img {
      display: block;
      width: 28em;
    }
  }

  .about__content {
    width: 50%;
    .content__title {
      font-size: 4em;
      font-weight: bold;
      margin-bottom: .5em;
    }

    .content__parrafo {
      font-size: 1.3em;
      margin-bottom: .8em;
      line-height: 1.2em;
    }

    .content__boton {
      font-size: 2em;
      margin-top: .9em;
      padding: 0.2em 1em;
    }
  }

  @media (max-width: 1034px) {font-size: .6rem;}
  @media (max-width: 720px) {
    width: 95%;

    font-size: .4rem;
    .about__content {
      width: 60%;
      font-size: 1.4em;
      text-align: justify;
      .content__boton {
        display: block;
        margin: .9em auto 0em auto;
      }
    }
  }
  @media (max-width: 520px) {
    width: 100%;
    flex-direction: column;
    font-size: .5rem;

    .about__content {
      order: 1;
      width: 90%;
      margin-bottom: 50px;
    }
    .container__about__logo {
      order: 2;
      .about__img {
        display: block;
        width: 25em;
      }
    }
  }
  @media (max-width: 360px) {

    .about__content {
      display: flex; 
      flex-direction: column;
      align-items: center;
    }
  }
  
}