.footerComponent {
  background-color: $blackColor;
  color: $lightYellow;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  .footerFlex {
    margin: 2em 0em;

    .footer__correo {
      font-size: 1.6em;
    }

    .footer__icon{
      display: flex;
      justify-content: space-around;

      .icon {
        fill: $lightYellow;
        fill: #f6c22d;
        width: 1.5em;
        margin: 1em 0vw;
      }
    }
  }
  @media (max-width: 720px) {
    .footerFlex {
      font-size: 0.9rem;
    }
  }
}