$timeAnimation:.2s;
.menuComponetContenedorBenefits{
    font-size: 1em;
    font-weight: bold;
  
    .menuComponet__header{
      color: $lightYellow;
      // color: red;
      background-color: $blackColor;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-radius: 100px;
      padding: 0.2em 0em 0.2em 1em;
      font-size: 2.1em;
      border: $lightYellow solid 2px;
      margin: 0.3em 0em;
      height: 2em;   
      cursor: pointer;

      .menuComponet__title {
        width: 100%;
        text-align: center;
        text-transform: uppercase;
      }
      
      .menuComponet__arrow {
        width: .8em;
        fill: #f6c22d;
        margin-right: 1em;
      }
    }
    @media (max-width: 920px) {
      .menuComponet__header{
        height: 2.5em;
      }  
      .menuComponet__body{
        font-size: 1.5em;

      }
    }
}

