.menuComponetContenedorMint{
  font-size: .7em;
  font-weight: bold;
  width: 35%;

  .menuComponet__header{
    color: $lightYellow;
    background-color: $blackColor;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 13px;
    padding: .5em 0em 0.5em 1em;
    font-size: 2em;
    border: 2px solid $lightYellow;
    border-width: 2px 0px 0px 0px;
    box-shadow: 0px 0px 1em -5px $blackColor;
    margin: 0em 2px;
    cursor: pointer;

    .menuComponet__title {
      width: 100%;
      text-align: center;
    }
    
    .menuComponet__arrow {
      width: .8em;
      fill: #f6c22d;
      margin-right: 1em;
      position: relative;
      z-index: 0;

      .--activePopUp{
        position: relative;
        z-index: -1;
      }
    }
  }

  .--borderRadio{
    border-radius: 100px;
    border: 2px solid $lightYellow;
  }

  .menuComponet__body{
    color: $blackColor;
    width: 95%;
    font-size: 2em;
    line-height: 1.2em;
    margin: 0.3em 0;
    text-align: justify;
    background-color:$blackColor;
    border: 2px solid $lightYellow;
    border-top: none;
    box-shadow: 0px 0px 1em -5px $blackColor;
    font-size: 1.8em;
  }
  .--disable{
    opacity: 0;
    pointer-events: none;
    margin: 0em auto;
    transform: translateY(-50px);
    .menuComponet__body__text {
      height: 0px;
      overflow: hidden;
      transition: height $timeAnimation ease-out;
    }
    transition: all $timeAnimation;
  }

  .--active {
    opacity: 1 ;
    //pointer-events: stroke ;   
    transform: translateY(-20px);
    transition: all 0.2s;
    //position: relative;
    z-index: -1;
    width: 100%;
    padding-top: 23px;
    margin-top: 20px;
    text-align: center;
    border-radius: 13px;
    border-top: none; 
    .menuComponet__body__text {
      height: auto;
      line-height: 1.8;
      overflow: hidden;
      color: $lightYellow;
      width: 100%;
    }
    transition: all $timeAnimation;
  }

  .button_mint{
    background-color: #333333;
    color: #f6c22d;
    font-size: 1em;
    border: none;
    padding-bottom: 10px;
  }


  @media (max-width: 720px) {
    width: 147px;
    .menuComponet__header{
      // height: 3.7em;
      .menuComponet__arrow { 
        width: 1.2em;
        margin: 0em 1em 0em 0.5em;
      }
    }
  }
} 