.boton {
  background-color: $blackColor;
  color: $lightYellow;
  padding: .7375em .4375em;
  border-radius: 5em;
  font-weight: bold;
  border: 2px solid $lightYellow;
  box-shadow: 0px 0px 1em -5px $blackColor;
  cursor: pointer;
}

.--arrow-down {
  transform: rotate(180deg);
  width: 1.9vw;
  fill: #f6c22d;
  margin-right: 1em;
}

.general__arrow {
  width: 0.7em;
  fill: #f6c22d;
  margin: 0em 0em 0em 0.7em;
}   
