.marketplace {
    font-size: 1rem;
    color:$blackColor ;
    width: 60%;
    margin: auto;
    position: relative;
    
    .marketplace__title {
        margin-top: 1.7em;
        font-size: 3vw;
        font-weight: bold;
    }

    .marketplace__card{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;

        .marketplace__card__content {
            font-size: 1vw;
            .marketplace__card__content__title {
                font-size: 2.5em;
                font-weight: bold;
            }
            .marketplace__card__content__copy {
                font-size: 1.3em;
                width: 87%;
                width: 24.3em;
                margin: 1em 0em 1em 0em;
                line-height: 1.2em;
            }
            .--marketplace__button {
                font-size: 1.8em;
                padding: 0.1em 1.2em 0.1em 1.2em;

            }
        }

      
            .marketplace__card__figure_img{
                width: 35%;
                border: 0.6em solid $lightYellow;
                border-radius: 71px;
                box-shadow: 0px 0px 6em 0px $blackColor;
            }
        
    }

    .--marketplace__footer{
        width: 80%;
        margin: auto;
        font-size: 1.5vw;
        text-align: center;
        padding: 0.7em 1.2em;
        margin-top: 3em;
        line-height: 1.3em;
    }
    .--order2 {
        order: 2;
        justify-content: space-between;
    }
}


@media (max-width: 1034px) {
    .marketplace {
        width: 70%;
        .marketplace__title{
            font-size: 2.5em;
        }
        .marketplace__card{ 
            .marketplace__card__content{
                font-size: .7em;
            }
        }
        .--marketplace__footer{  
            font-size: 1em;        
        }
    }
}


@media (max-width: 917px) {
    .marketplace {
        width: 80%;

        .marketplace__title{
            font-size: 2.4em;
            margin-bottom: 30px;
        }

        .marketplace__card{
            flex-direction: column;
            .marketplace__card__content{
                font-size: .6em;
                margin-bottom: 7em;
                .marketplace__card__content__title {
                    font-size: 3.5em;
                    margin-top: 1em;
                }
                .marketplace__card__content__copy {
                    font-size: 1.7em;
                    width: 100%;
                    line-height: 1.4em;
                }
                .--marketplace__button {
                    font-size: 2.5em;
                }
            }

            .marketplace__card__figure_img{
                width: 60%;
                margin-bottom: 30px;
            }
        }
        .--marketplace__footer{  
            font-size: .8em;   
            width: 100%;     
        }
        .--order2 {
            order: 0;
        }
    }


}

@media (max-width: 520px) {
    .marketplace {
        width: 90%;

        text-align: center;
        .marketplace__title{
            font-size: 2em;
        }

        .marketplace__card{
            flex-direction: column;
            .marketplace__card__content{
            margin-bottom: 7em;
            font-size: .5em;
      
                .marketplace__card__content__copy {
                    width: 100%;
                    margin: 1em 0em;
                } 
            }
            .marketplace__card__figure_img{
                width: 70%;
                margin-bottom: 30px;
                border-radius: 40px;
                box-shadow: 0px 0px 2em 0px $blackColor;

            }
        }
        .--marketplace__footer{  
            width: 100%;
            font-size: .8em;        
        }
    }
}
