  .--botonRoadMap {
    font-size: 2.8vw;
    border-radius: 20px;
    display: block;
    margin: 2em auto 2em auto;
    padding: 1vw 1vw 1vw 1vw;
  }

  .--img2Visible{
    height: auto !important;
  }

  .roadmap__body__section {
    width: 50%;
    margin: auto;

    .roadmap__body__title{
      font-size: 4vw;
      width: 38vw;
      text-align: center;
      margin: auto;
    }
  
    .roadMap__video {
      width: 100%;
      height: auto;
      border: 1px solid $lightYellow;
      border-radius: 10px;
      margin: 3em auto;
      cursor: pointer;
      position: relative;
      z-index: 100;
      .video{
        width: 100%;
        border-radius: 10px;
      }
      .iconPlay{
        width: 100px;
        height: 100px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    }
    .roadmap__video__descripcion {
      font-size: 1.2vw;
      line-height: 29px;
      margin: 1em auto;
    }
  }

